<template>
  <div class="profile-page">
    <section class="section-profile-cover section-shaped my-0">
      <div class="shape shape-style-1 shape-primary shape-skew alpha-4">
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
      </div>
      <div class="container shape-container d-flex align-items-center">
        <div class="col px-0">
          <div class="row justify-content-center align-items-center">
            <div class="col-lg-12 text-center" style="pading-bottom:50">
              <h1 class="text-white">SPIRITUAL VITAMINS</h1>
            </div>
          </div>
        </div>
      </div>
    </section>

    <section class="section section-skew">
      <div class="container">
        <card shadow class="card-profile mt--300" no-body>
          <div class="px-1">
            <!-- <div class="row justify-content-center">
              <div class="col-lg-3 order-lg-2"></div>
              <div class="col-lg-3 order-lg-3 text-lg-right align-self-lg-center"></div>
              <div class="col-lg-3 order-lg-1">
                <div class="card-profile-stats d-flex justify-content-center">
                  <div>
                    <span class="heading">{{ dev.hearts.length}}</span>
                    <span class="description">Love it</span>
                  </div>
                  <div>
                    <span class="heading">{{ dev.lesson.length}}</span>
                    <span class="description">Learning it</span>
                  </div>
                  <div>
                    <span class="heading">{{ dev.working.length}}</span>
                    <span class="description">Working on it</span>
                  </div>
                  <div>
                    <span class="heading">{{ dev.beenthere.length}}</span>
                    <span class="description">Been there</span>
                  </div>
                </div>
              </div>
            </div>-->
            <div class="text-center mt-5">
              <!-- <h4>Today's Spiritual Vitamins</h4> -->
              <h3>{{dev.title}}</h3>
              <div>
                <i class="ni education_hat mr-2"></i>
                {{timestamp}}
              </div>
            </div>
            <div class="mt-5 py-5 border-top text-center">
              <div class="row justify-content-center">
                <div class="col-lg-9">
                  <h3>Summary</h3>
                  <p>{{ dev.summery}}</p>
                </div>
                <div style="padding-top:50px" class="sharethis-inline-share-buttons"></div>
              </div>
            </div>
          </div>
        </card>
      </div>
    </section>

    <section section-skew>
      <div class="row">
        <div style="padding-left:40px;  padding-right:40px;" class="col-md-8 cl-sm-12">
          <card shadow style="padding:5px; margin-bottom:20px;">
            <div class="text mt-5">
              <!-- <h4>Main Teaching for {{dev.title}}</h4> -->

              <div v-for="item in dev.bibleReading" :key="item.id">
                <h5>{{ item }}</h5>
              </div>

              <div v-html="dev.teaching" style="margin-top:10px" class="border-top"></div>
              <div style="margin-top:10px;">
                <h3 class="col-sm-12" style="text-algin:center">Leave us a feedback below</h3>
                <div v-if="showLikeDiv">
                  <button
                    @click="addLike('like')"
                    style="margin:5px;"
                    class="btn btn-success btn-icon col-md-3"
                  >I love this</button>

                  <button
                    @click="addLike('learn')"
                    style="margin:5px;"
                    class="btn btn-info btn-icon col-md-3"
                  >I learned something</button>

                  <button
                    @click="addLike('working')"
                    style="margin:5px;"
                    class="btn btn-primary btn-icon col-md-3"
                  >I am working on it</button>

                  <button
                    @click="addLike('beenThere')"
                    style="margin:5px;"
                    class="btn btn-warning btn-icon col-md-3"
                  >I have been here</button>
                </div>
              </div>
              <!-- 
              <div style="margin-top:10px;">
                <h3 class="col-sm-12" style="text-algin:center">Leave us a feedback below</h3>
                
              </div>-->
              <modal
                :show.sync="modals.modal2"
                gradient="danger"
                modal-classes="modal-danger modal-dialog-centered"
              >
                <h6
                  slot="header"
                  class="modal-title"
                  id="modal-title-notification"
                >Your Feed back has been successfully added</h6>

                <div class="py-3 text-center">
                  <i class="ni ni-bell-55 ni-3x"></i>
                  <h4 class="heading mt-4">Thank you for your feedback</h4>
                  <p>{{ message }}</p>
                </div>

                <template slot="footer">
                  <base-button
                    type="link"
                    text-color="white"
                    class="ml-auto"
                    @click="modals.modal2 = false"
                  >Close</base-button>
                </template>
              </modal>
            </div>
          </card>

          <card shadow style="padding:5px; margin-bottom:20px;">
            <div>
              <h4>Leave us a comment</h4>
              <base-input class="mb-4">
                <textarea
                  class="form-control form-control"
                  v-model.lazy="comment"
                  rows="3"
                  cols="80"
                  placeholder="Type your comment here..."
                ></textarea>
              </base-input>
              <button
                @click.prevent="addComment(comment)"
                v-if="showLikeButton"
                style="margin:5px;"
                class="btn btn-outline-success btn-icon col-md-3"
              >Send Comment</button>
            </div>

            <modal
              :show.sync="modals.modal3"
              gradient="danger"
              modal-classes="modal-danger modal-dialog-centered"
            >
              <h6 slot="header" class="modal-title" id="modal-title-notification">{{ modalTitle }}</h6>

              <div class="py-3 text-center">
                <i class="ni ni-bell-55 ni-3x"></i>
                <h4 class="heading mt-4">{{ message }}</h4>
              </div>

              <template slot="footer">
                <base-button
                  type="link"
                  text-color="white"
                  class="ml-auto"
                  @click="modals.modal3 = false"
                >Close</base-button>
              </template>
            </modal>
          </card>
        </div>

        <div
          style="padding-right:40px; padding-left:40px; padding-bottom:39px;"
          class="col-md-4 cl-sm-12"
        >
          <card shadow style="margin-bottom:20px;">
            <div style="padding:10px;" class="row">
              <div>
                <div class="row">
                  <h2 class="col-sm-12 col-md-12 col-lg-12">My Bible</h2>
                  <div class="col-sm-12 col-md-12 col-lg-12">
                    <button
                      class="btn btn-outline-primary btn-icon"
                      @click.prevent="showBible"
                    >Load Bible</button>
                  </div>
                </div>
              </div>
            </div>
            <div style="padding-top:20px">
              <biblia:bible
                layout="minimal"
                resource="nirv"
                width="100%"
                height="380px"
                historyButtons="false"
                resourcePicker="true"
                shareButton="false"
                textSizeButton="true"
                :startingReference="dev.mainReading"
              ></biblia:bible>
            </div>
          </card>
          <card shadow style="padding-top:10px">
            <div>
              <div>
                <h2 class="border-bottom">My Notes</h2>
                <!-- <h4>Always take notes, its important! Fill in the following to record what you learned:</h4> -->
                <div style="padding-top:10px">
                  <h4>Command</h4>
                  <base-input class="mb-4">
                    <textarea
                      class="form-control form-control"
                      v-model.lazy="notepad.command"
                      rows="2"
                      cols="80"
                      placeholder="What was the command embedded in the word today?"
                    ></textarea>
                  </base-input>
                </div>
                <div style="padding-top:10px">
                  <h4>Applications</h4>
                  <base-input class="mb-4">
                    <textarea
                      class="form-control form-control"
                      v-model.lazy="notepad.application"
                      rows="2"
                      cols="80"
                      placeholder="How would you apply what you learned today in your life?"
                    ></textarea>
                  </base-input>
                </div>
                <div style="padding-top:10px">
                  <h4>Rhema</h4>
                  <base-input class="mb-4">
                    <textarea
                      class="form-control form-control"
                      v-model.lazy="notepad.rhema"
                      rows="2"
                      cols="80"
                      placeholder="Rhema means an utterance or thing said in Greek. What did God communicate to you?"
                    ></textarea>
                  </base-input>
                </div>
                <div style="padding-top:10px">
                  <h4>Promises</h4>
                  <base-input class="mb-4">
                    <textarea
                      class="form-control form-control"
                      v-model.lazy="notepad.promise"
                      rows="2"
                      cols="80"
                      placeholder="What promises were embedded in the word today? "
                    ></textarea>
                  </base-input>
                </div>
                <button
                  @click.prevent="printDiv"
                  style="margin:5px;"
                  class="btn btn-outline-primary btn-icon col-md-12"
                >Print</button>
                <button
                  @click.prevent="saveToAccount"
                  style="margin:5px;"
                  class="btn btn-outline-danger btn-icon col-md-12"
                >Save to account</button>

                <button
                  @click.prevent="sendEmail"
                  style="margin:5px;"
                  class="btn btn-outline-success btn-icon col-md-12"
                >Send as email</button>
              </div>
            </div>
          </card>
        </div>
      </div>
      <div style="padding:50px">
        <base-alert
          v-if="saved"
          @click.prevent="!saved"
          type="success"
          icon="ni ni-like-2"
          dismissible
        >
          <span slot="text">
            <strong>Success!</strong> your notes have been saved to your account
          </span>
        </base-alert>
      </div>
    </section>

    <div id="divToPrint" style="display:none;">
      <div
        style="padding: 20px; background: #a900bf; color: #fff !important ; font-family:sans-serif !important;"
      >
        <h2>Here are your notes</h2>
      </div>
      <div>
        <h3 style="font-family:sans-serif !important;">{{ dev.title }}</h3>
        <h4>Bible readings: {{ dev.bibleReading }}</h4>
        <h4>Spiritual Vitamins Summary</h4>
        <p style="font-family:sans-serif !important;">{{ dev.summery }}</p>
        <h4>Command</h4>
        <p>{{ notepad.command }}</p>
        <h4>Applications</h4>
        <p>{{ notepad.application }}</p>
        <h4>Rhema</h4>
        <p>{{ notepad.rhema }}</p>
        <h4>Promise</h4>
        <p>{{ notepad.promise }}</p>
      </div>

      <footer
        style="padding: 20px; background: #a900bf; color: #fff !important ; font-family:sans-serif !important;"
      >
        <h6>love DNTSTWG</h6>
      </footer>
    </div>
  </div>
</template>
<script>
import { db } from "../firebaseConfig";
import Modal from "@/components/Modal.vue";

export default {
  components: {
    Modal
  },
  data() {
    return {
      modals: {
        modal1: false,
        modal2: false,
        modal3: false
      },
      comment: "",
      modalTitle: "",
      message: "",
      isLoggedin: false,
      emailBody: "",
      dev: {},
      likes: [],
      timestamp: "",
      day: null,
      month: null,
      year: null,
      like: "",
      saved: this.saved,
      loggedin: this.userLogedIn,
      userM: this.user,
      notepad: {
        command: "",
        application: "",
        rhema: "",
        promise: ""
      },
      bibleReading: {},
      showLikeButton: true,
      showLikeDiv: true,
      showB: false,
      showBtext: "Show bible"
    };
  },
  methods: {
    showBible() {
      let hover = document.createElement("script");
      hover.setAttribute(
        "src",
        "https://www.blueletterbible.org/assets/scripts/blbToolTip/BLB_ScriptTagger-min.js"
      );
      document.head.appendChild(hover);

      let bible = document.createElement("script");
      bible.setAttribute("src", "//biblia.com/api/logos.biblia.js");
      document.head.appendChild(bible);
      logos.biblia.init();
      // if (this.showB == false) {
      //   this.showB = true;
      //   this.showBtext = "Hide Bible";
      // } else {
      //   this.showB = false;
      //   this.showBtext = "Show bible";
      // }
      let share = document.createElement("script");
      share.setAttribute(
        "src",
        "async",
        "https://platform-api.sharethis.com/js/sharethis.js#property=5eb011344ba4f9001384cb50&product=inline-share-buttons"
      );
      document.head.appendChild(share);
    },
    sendEmail() {
      var body = document.getElementById("divToPrint").innerHTML;
      window.open(
        "mailto:info@dntstwg.com?subject=Notes from todays Spiritual Vitamins&body=Hello there I just took my Spiritual Vitamins entitled " +
          this.dev.title +
          ". In summary, it touched on: , " +
          this.dev.summery +
          ". These are my notes from todays Spiritual Vitamins: The command I received from this is: " +
          this.notepad.command +
          ". The way I would apply this is: " +
          this.notepad.application +
          ". My rhema which means an utterance or thing said in Greek is: " +
          this.notepad.rhema +
          ". And finally the promise I got from today's reading is: " +
          this.notepad.promise +
          ". The Bible reading(s) for this Spiritual Vitamins is/are comming from: " +
          this.dev.bibleReading
      );
    },

    addLike(like) {
      if (this.user) {
        if (like === "like") {
          this.showLikeDiv = false;
          db.collection("devotions")
            .doc(this.dev.id)
            .collection(like)
            .doc(this.user.uid)
            .get()
            .then(doc => {
              if (doc.exists) {
                this.modalTitle = "Note";
                this.message =
                  "Sorry " +
                  this.user.displayName +
                  " you have already added this feedback";
                this.modals.modal3 = true;
                this.showLikeDiv = true;
              } else {
                db.collection("devotions")
                  .doc(this.dev.id)
                  .collection(like)
                  .doc(this.user.uid)
                  .set({
                    id: this.user.uid,
                    name: this.user.displayName,
                    date: new Date(Date.now()).toLocaleString("en-ZM")
                  })
                  .then(data => {
                    this.message =
                      "We are glad to hear you loved todays Spiritual Vitamins, we hope it equips you for every good work.";
                    this.modals.modal2 = true;
                    this.showLikeDiv = true;
                  });
              }
            });
        } else if (like === "learn") {
          this.showLikeDiv = false;
          db.collection("devotions")
            .doc(this.dev.id)
            .collection(like)
            .doc(this.user.uid)
            .get()
            .then(doc => {
              if (doc.exists) {
                this.modalTitle = "Note";
                this.message =
                  "Sorry " +
                  this.user.displayName +
                  " you have already added this feedback";
                this.modals.modal3 = true;
                this.showLikeDiv = true;
              } else {
                db.collection("devotions")
                  .doc(this.dev.id)
                  .collection(like)
                  .doc(this.user.uid)
                  .set({
                    id: this.user.uid,
                    name: this.user.displayName,
                    date: new Date(Date.now()).toLocaleString("en-ZM")
                  })
                  .then(data => {
                    this.message =
                      "We thank God that you learned something new from todays Spiritual Vitamins, we pray that Gods word will accomplish its intended purpose in you!";
                    this.modals.modal2 = true;
                    this.showLikeDiv = true;
                  });
              }
            });
        } else if (like === "working") {
          this.showLikeDiv = false;
          db.collection("devotions")
            .doc(this.dev.id)
            .collection(like)
            .doc(this.user.uid)
            .get()
            .then(doc => {
              if (doc.exists) {
                this.modalTitle = "Note";
                this.message =
                  "Sorry " +
                  this.user.displayName +
                  " you have already added this feedback";
                this.modals.modal3 = true;
                this.showLikeDiv = true;
              } else {
                db.collection("devotions")
                  .doc(this.dev.id)
                  .collection(like)
                  .doc(this.user.uid)
                  .set({
                    id: this.user.uid,
                    name: this.user.displayName,
                    date: new Date(Date.now()).toLocaleString("en-ZM")
                  })
                  .then(data => {
                    this.message =
                      "Remember that Gods grace is sufficient for you in whatever season. Keep pushing, look to Jesus the author and finisher of your faith and know that God will always be there to uphold you! ";
                    this.modals.modal2 = true;
                    this.showLikeDiv = true;
                  });
              }
            });
        } else if (like === "beenThere") {
          this.showLikeDiv = false;
          db.collection("devotions")
            .doc(this.dev.id)
            .collection(like)
            .doc(this.user.uid)
            .get()
            .then(doc => {
              if (doc.exists) {
                this.modalTitle = "Note";
                this.message =
                  "Sorry " +
                  this.user.displayName +
                  " you have already added this feedback";
                this.modals.modal3 = true;
                this.showLikeDiv = true;
              } else {
                db.collection("devotions")
                  .doc(this.dev.id)
                  .collection(like)
                  .doc(this.user.uid)
                  .set({
                    id: this.user.uid,
                    name: this.user.displayName,
                    date: new Date(Date.now()).toLocaleString("en-ZM")
                  })
                  .then(data => {
                    this.message =
                      "We defeat the enemy by the blood of the Lamb and the word of our testimony. If you have gone through something and were comforted by God, it is for you to comfort others that may experience the same thing so please do share your experience with others. ";
                    this.modals.modal2 = true;
                    this.showLikeDiv = true;
                  });
              }
            });
        }
      } else {
        this.modalTitle = "Note";
        this.message = "Please Login to add a feedback";
        this.modals.modal3 = true;
      }
    },
    addComment(comment) {
      if (this.user) {
        this.showLikeButton = false;
        var timestamp = Date.now();

        db.collection("devotions")
          .doc(this.dev.id)
          .collection("comments")
          .doc(this.user.uid)
          .get()
          .then(docSnapshot => {
            if (docSnapshot.exists) {
              this.modalTitle = "Note!!!";
              this.message =
                "Sorry you have already commented on this devotion, you cannot comment again, thank you";
              this.modals.modal3 = true;
              this.showLikeButton = true;
            } else {
              db.collection("devotions")
                .doc(this.dev.id)
                .collection("comments")
                .doc(this.user.uid)
                .set({
                  comment: comment,
                  email: this.user.email,
                  name: this.user.displayName,
                  date: new Date(timestamp).toLocaleString("en-ZM"),
                  userId: this.user.uid
                })
                .then(response => {
                  this.modalTitle =
                    "Thanks for your comment " + this.user.displayName;
                  this.message = "Your thoughts are important to us.";
                  this.modals.modal3 = true;
                  this.showLikeButton = true;
                });
            }
          });
      } else {
        this.modalTitle = "Note";
        this.message = "Please login to add a comment";
        this.modals.modal3 = true;
      }
    },
    printDiv() {
      var divToPrint = document.getElementById("divToPrint");
      var popupWin = window.open("", "_blank", "width=300,height=300");
      popupWin.document.open();
      popupWin.document.write(
        '<html><body onload="window.print()">' +
          divToPrint.innerHTML +
          "</html>"
      );
      popupWin.document.close();
    },

    saveToAccount() {
      if (this.user) {
        db.collection("users")
          .doc(this.user.uid)
          .collection("notes")
          .add({
            command: this.notepad.command,
            application: this.notepad.application,
            rhema: this.notepad.rhema,
            promise: this.notepad.promise,
            devSummery: this.dev.summery,
            devTitle: this.dev.title,
            devBibleReading: this.dev.bibleReading,
            devDate: this.dev.timestamp,
            noteDate: Date.now()
          })
          .then(response => {
            this.saved = true;
          });
      } else {
        this.modalTitle = "Note!!!";
        this.message = "Please Login to Save note to account";
        this.modals.modal3 = true;
      }
    }
  },
  computed: {
    saved: false,
    user() {
      return this.$store.getters.user;
    },
    userLogedIn() {
      return this.$store.getters.loggedIn;
    }
  },
  created() {
    this.saved = false;

    db.collection("devotions")
      .orderBy("timestamp", "desc")

      .limit(1)
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          let data = doc.data();
          data.id = doc.id;
          let dev = [];
          dev.push(data);
          this.dev = dev[0];
          const options = {
            weekday: "long",
            year: "numeric",
            month: "long",
            day: "numeric"
          };
          this.timestamp = new Date(this.dev.timestamp).toLocaleDateString(
            undefined,
            options
          );
          console.log(this.dev);
          let bible = document.createElement("script");
          let hover = document.createElement("script");
          hover.setAttribute(
            "src",
            "https://www.blueletterbible.org/assets/scripts/blbToolTip/BLB_ScriptTagger-min.js"
          );
          document.head.appendChild(hover);

          bible.setAttribute("src", "//biblia.com/api/logos.biblia.js");
          document.head.appendChild(bible);
          logos.biblia.init();
          fetch(
            "https://ajith-holy-bible.p.rapidapi.com/GetVerses?Book=Luke&chapter=1&VerseFrom=5&VerseTo=8",
            {
              method: "GET",
              headers: {
                "x-rapidapi-host": "ajith-holy-bible.p.rapidapi.com",
                "x-rapidapi-key":
                  "a781cf35aemsh6653fbfd883e4b7p13299ejsnc299bfc9e320"
              }
            }
          )
            .then(response => {
              console.log(response);

              this.bibleReading = response;
            })
            .catch(err => {
              console.log(err);
            });
        });
      });
  },
  mouonted() {
    let hover = document.createElement("script");
    hover.setAttribute(
      "src",
      "https://www.blueletterbible.org/assets/scripts/blbToolTip/BLB_ScriptTagger-min.js"
    );
    document.head.appendChild(hover);

    // let bible = document.createElement("script");
    // bible.setAttribute("src", "//biblia.com/api/logos.biblia.js");
    // document.head.appendChild(bible);
    // logos.biblia.init();

    // let share = document.createElement("script");
    // share.setAttribute(
    //   "src",
    //   "async",
    //   "https://platform-api.sharethis.com/js/sharethis.js#property=5eb011344ba4f9001384cb50&product=inline-share-buttons"
    // );
    // document.head.appendChild(share);
    // logos.biblia.init();
  }
};
</script>
<style>
</style>
