<script>
import Modal from "@/components/Modal.vue";
import { db } from "../firebaseConfig";
import VideoBg from "vue-videobg";
export default {
  name: "",
  email: "",
  message: "",
  components: { Modal, VideoBg },
  data() {
    return {
      name: "",
      modals: {
        modal1: false,
        modal2: false,
        modal3: false
      },
      site: {}
    };
  },
  methods: {
    sendEmail() {
      window.open(
        "mailto:info@dntstwg.com?subject=Feedback&body=Hello my name is " +
          this.name +
          ", my message is, " +
          this.message
      );
    }
  },
  created() {
    db.collection("site")
      .doc("details")
      .onSnapshot(data => {
        console.log(data);
        this.site = data.data();
      });
  }
};
</script>

<style lang="scss">
.image-area {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  position: absolute;
}

.img-wrapper {
  width: 300px;
  height: 400px;
  position: relative;
  overflow: hidden;
}

.img-wrapper:before {
  content: "";
  position: absolute;
  top: 0;
  left: 180%;
  height: 100%;
  width: 100%;
  z-index: 1;
  transform: skew(45deg);
  transition: 0.5s;
}

.img-wrapper:hover:before {
  left: -180%;
}

.img-wrapper img {
  height: 400px;
  width: 300px;
  transition: 2s;
}
.img-wrapper:hover img {
  filter: grayscale(0%);
  transform: scale(1.1);
}

.img-wrapper h2 {
  background: purple;
  font-family: Poppins;
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  margin: 0;
  padding: 10px 0;
  position: absolute;
  bottom: 0;
  width: 100%;
  transform: perspective(400px) rotateY(90deg);
  transform-origin: right;
  transition: 1s;
}

.img-wrapper:hover h2 {
  transform: perspective(400px) rotateY(0deg);
}

.img-wrapper ul {
  position: absolute;
  top: 0;
  left: 0;
  margin: 0;
  padding: 0;
  list-style: none;
}

.img-wrapper ul li {
  background: rgb(236, 236, 236);
  height: 500px;
  width: 300px;
  padding: 10px;
  text-align: start;
  line-height: 40px;
  transform: perspective(800px) rotateY(90deg);
  transition: 0.5s;
  transform-origin: left;
}

.img-wrapper:hover ul li {
  transform: perspective(800px) rotateY(0deg);
}

.img-wrapper:hover ul li:nth-child(1) {
  transition-delay: 0.2s;
}

.img-wrapper:hover ul li:nth-child(2) {
  transition-delay: 0.6s;
}

.img-wrapper:hover ul li:nth-child(3) {
  transition-delay: 0.8s;
}

.img-wrapper:hover ul li:nth-child(4) {
  transition-delay: 1s;
}

.img-wrapper ul li a {
  color: purple;
  background: rgba(255, 255, 255, 0);
}

.img-wrapper ul li i {
  color: purple;
  background: rgba(255, 255, 255, 0);
}
</style>
