<script>
import { db } from "../firebaseConfig";
import Modal from "@/components/Modal.vue";
export default {
  components: {
    Modal
  },
  computed: {
    saved: false,
    user() {
      return this.$store.getters.user;
    },
    userLogedIn() {
      return this.$store.getters.loggedIn;
    }
  },
  data() {
    return {
      modals: {
        modal1: false,
        modal2: false,
        modal3: false
      },
      notes: [],
      print: {
        summery: "",
        title: "",
        date: "",
        bibleReading: "",
        promise: "",
        rhema: "",
        command: "",
        application: "",
        name: ""
      },
      userM: this.user
    };
  },
  created() {
    console.log(this.user);
    db.collection("users")
      .doc(this.user.uid)
      .collection("notes")
      .orderBy("noteDate", "desc")
      .limit(10)
      .onSnapshot(data => {
        data.forEach(doc => {
          this.notes.push(doc.data());
        });
      });
  },
  methods: {
    deleteNote(id) {
      if (confirm("Are you sure you want to delete this Note?")) {
        // Save it!
        db.collection("users")
          .doc(this.user.uid)
          .collection("notes")
          .doc(id)
          .delete()
          .then(response => {
            alert("Your note was succesfully delted");
          });
      } else {
        // Do nothing!
      }
    },
    printDiv(
      summery,
      devTitle,
      noteDate,
      devBibleReading,
      promise,
      rhema,
      command,
      application
    ) {
      this.print.summery = summery;
      this.print.command = command;
      this.print.title = devTitle;
      this.print.promise = promise;
      this.print.application = application;
      this.print.rhema = rhema;

      var divToPrint = document.getElementById("divToPrint");
      var popupWin = window.open("", "_blank", "width=300,height=300");
      popupWin.document.open();
      popupWin.document.write(
        '<html><body onload="window.print()">' +
          divToPrint.innerHTML +
          "</html>"
      );
      popupWin.document.close();
    }
  }
};
</script>
