<script>
import { db } from "../firebaseConfig";
import firebase from "firebase";
import VideoBg from "vue-videobg";
export default {
  name: "home",
  components: { VideoBg },
  data() {
    return {
      videoId: "yCwmDoeweNU",
      site: {},
      signedIn: false,
      dev: {},

      days: "",
      hours: "",
      minutes: "",
      seconds: "",
      launch: null,
      timestamp: null
    };
  },
  methods: {
    playVideo() {
      this.player.playVideo();
    },
    playing() {
      console.log("o/ we are watching!!!");
    },
    countDown() {}
  },
  computed: {
    user() {
      return this.$store.getters.user;
    },
    userLogedIn() {
      return this.$store.getters.loggedIn;
    },
    player() {
      return this.$refs.youtube.player;
    }
  },
  mounted() {
    let hover = document.createElement("script");
    hover.setAttribute(
      "src",
      "https://www.blueletterbible.org/assets/scripts/blbToolTip/BLB_ScriptTagger-min.js"
    );
    document.head.appendChild(hover);
  },
  created() {
    let hover = document.createElement("script");
    hover.setAttribute(
      "src",
      "https://www.blueletterbible.org/assets/scripts/blbToolTip/BLB_ScriptTagger-min.js"
    );
    document.head.appendChild(hover);

    db.collection("devotions")
      .orderBy("timestamp", "desc")
      .limit(1)
      .onSnapshot(querySnapshot => {
        querySnapshot.forEach(doc => {
          let data = doc.data();
          data.id = doc.id;
          let dev = [];
          dev.push(data);
          this.dev = dev[0];
          this.timestamp = new Date(this.dev.timestamp).toLocaleString("en-ZM");
          console.log(this.dev);

          var deadline = this.dev.nextDev;
          var x = setInterval(function() {
            var now = new Date().getTime();
            var t = deadline - now;
            var days = Math.floor(t / (1000 * 60 * 60 * 24));
            this.days = days;
            var hours = Math.floor(
              (t % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)
            );
            var minutes = Math.floor((t % (1000 * 60 * 60)) / (1000 * 60));
            var seconds = Math.floor((t % (1000 * 60)) / 1000);
            document.getElementById("demo").innerHTML =
              days + "d " + hours + "h " + minutes + "m " + seconds + "s ";
            if (t < 0) {
              clearInterval(x);
              document.getElementById("demo").innerHTML =
                "Next Countdown will start shortly";
            }
          }, 1000);
        });
      });
  }
};
</script>
