d-breathed and is useful for teaching, rebuking, correcting and training in righteousness, so that the servant of God may be thoroughly equipped for every good work. Simply put, the word of God should be active in our lives to work on our hearts, attitudes and thoughts, and to equip, teach, rebuke, correct and train us.")]),_c('p',{staticClass:"text-white"},[_vm._v("Every day on this site, as a way to encourage others to spend time with God, we post what we call Spiritual Vitamins. Spiritual Vitamins is a written post based on a scripture/chapter from the Bible. The written piece is the rhema God gives our writers based on the scripture/chapter from the Bible. All are encouraged to read the scripture/chapter from the Bible themselves and journal what rhema God gives them. Through spending time with God, we believe that characters will be built, people will become dependent on God, people will cherish the time spent with God and this will help us to grow into the full manifestation of what God has purposed for us, in Christ.")]),_c('p',{staticClass:"text-white"},[_vm._v("We invite you to commit to spending time with God and see for yourself how God will truly work on you (heart, mind, spirit, soul)!")]),_c('p'),_c('p')])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"shape shape-style-3 bg-gradient-default shape-skew",staticStyle:{"backgroud-color":"#ffc107 !importanat"}},[_c('span'),_c('span'),_c('span'),_c('span')])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row text-center justify-content-center"},[_c('div',{staticClass:"col-lg-10"},[_c('h2',{staticClass:"display-3 text-white"},[_vm._v("DONT KNOW WHERE TO START ?")])])])}]

